.recipe {
    border-radius: 10px;
    background: #fff;
    margin: 20px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    text-align: center;
    align-items: center;
    min-width: 40%;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}

.image {
    border-radius: 15px;
    width: 90%;
}

.list {
    list-style: none;
}

.calories {
    font-size: 20px;
    font-family: fantasy;
    letter-spacing: 2px;
    color: rgb(175, 170, 170);
}

.ul {
    padding: 7px;
    font-size: 12px;
}