.App {
  min-height: 100vh;
  background: rgb(238,235,221);
  background: radial-gradient(circle, rgba(238,235,221,1) 0%, rgba(223,207,208,1) 95%);
}

.search-form {
  min-height: 10vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.search-bar {
  border: none;
  height: 5vh;
  width: 40vw;
  border-radius: 15px 0px 0px 15px;
  text-align: center;
  font-size: 15px;
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}

.search-btn {
  border: none;
  height: 5vh;
  width: 86px;
  border-radius: 0px 15px 15px 0px;
  cursor: pointer;
  font-weight: 900;
  font-family: arial;
  color: #fff;
  background-color: darkslategray;
}

.singleRec {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  flex-wrap: wrap;
}

.backBtn {
  height: 30px;
  position: absolute;
  top: 2%;
  left: 2%;
}